<template>
  <h1>Countdown</h1>
  <h2>{{ countdownString }}</h2>
</template>

<script>
export default {
  name: "countDownTimer",
  data() {
    return {
      countdownString: '',
      targetDate: new Date('2023-06-03T15:00:00') // Ziel Datum und Uhrzeit
    }
  },
  methods: {
    updateCountdown() {
      const now = new Date();
      const differenceInMilliseconds = this.targetDate - now;
      if (differenceInMilliseconds < 0) {
        this.countdownString = "The party has started";
        return;
      }

      const seconds = Math.floor((differenceInMilliseconds / 1000) % 60);
      const minutes = Math.floor((differenceInMilliseconds / 1000 / 60) % 60);
      const hours = Math.floor((differenceInMilliseconds / (1000 * 60 * 60)) % 24);
      const days = Math.floor(differenceInMilliseconds / (1000 * 60 * 60 * 24));

      this.countdownString = `${days} Tage, ${hours} Stunden, ${minutes} Minuten und ${seconds} Sekunden`;
    }
  },
  mounted() {
    this.updateCountdown();
    setInterval(this.updateCountdown, 1000); // aktualisieren jede Sekunde
  }
}
</script>

